                            <span class="invalid-feedback"> {{ errors[0] }} </span>
                            <br>
                            </validation-provider>

                            <validation-provider rules="required|min:10" v-slot="{ errors , classes }">
                            <label for="signupTel" class="h5 text-secondary"></label>
                            <input type="tel" id="signupTel" name="" :class="classes" v-model="signupData.tel" maxlength="10">
                            <span class="invalid-feedback"> {{ errors[0] }} </span>
                            <br>
                            </validation-provider>

                            <div class="form-check">
                            <input class="form-check-input" type="checkbox" id="checkBtn" name="" v-model="signupData.checkRule">
                            <label class="form-check-label" for="checkBtn">
                                
