                            </label>
                            </div>
                            
                            <div class="d-flex justify-content-between mt-4">
                                <a href="#" class="tologinBtn align-self-center" @click.prevent="signupCardisShow = !signupCardisShow"></a>
                                <button type="submit" class="btn btn-primary text-white" :disabled="invalid" :class="{'pageDisabled' : !signupData.checkRule}"></button>
