<script>

import $ from 'jquery';

export default {
    data() {
        return {
            signupCardisShow : false,
            user : {
                username : '',
                password : ''
            },
            isLoading : false,
            signupData : {
                name : '',
                password : '',
                email : '',
                address : '',
                tel : '',
                checkRule : false
            }
        }
    },
    methods : {
        openPage(){

        setTimeout(() => {
            $('.curtain').addClass('curtainShow');
        }, 5);

        },
        signIn(){
            const self = this;
            const api = `${process.env.APIPATH}/admin/signin`;
            
            self.isLoading = true;
            self.$http.post(api , self.user).then((res) => {

            if(res.data.success){
                const token = res.data.token;
                const expired = res.data.expired;
                document.cookie = `youxiaaaToken=${ token }; expires=${ new Date(expired) };`;
                self.$router.push('/admin/products');
            }else{
                self.isLoading = false;
                alert(res.data.message)
            }
            })
        },
        sendSignup(){
            const self = this;
            alert(`${self.signupData.email} `);
        },
        forGetpw(){
            alert(` `);
        }
    },
    created(){
        this.openPage();
    }
}
