<template>
    <div>

        <!--  -->
        <section>

        <div class="curtain"></div>

        <div class="rightCurtain1"></div>
        <div class="rightCurtain2"></div>

        </section>
        <!--  -->
       
        <!-- title -->
        <section>

           <div class="row marginpaddingFix">
               <div class="col marginpaddingFix">
                    <div class="aboutTitle preFadein d-flex justify-content-center align-items-center">
                        <div>
                        <h2 class="h1 text-primary text-center">Who we are</h2>
                        <img src="static/img/aboutus.svg" alt="">
                        </div>
                    </div>
               </div>
           </div>

        </section>
        <!--  -->

       <section>

           <div class="aboutContent aboutContainer">

           <div class="row timeLine aboutContentItem flex-row-reverse preFadein">
               
               <div class="col-lg-6">
                    <h3 class="h2 text-primary">
                        2015/02 - 
                    </h3>
                    <p class="h6 lh-global text-secondary">
                        <span class="text-primary"></span><span class="text-primary"></span>
                    </p>
               </div>
               <div class="col-lg-6 text-center  d-flex justify-content-center align-items-center">
                       <img src="static/img/about1.svg" alt="">
               </div>

           </div>

            <div class="row timeLine aboutContentItem preFadeinTop">
               
               <div class="col-lg-6">
                    <h3 class="h2 text-primary text-right">
                        2019/01 - 
                    </h3>
                    <p class="h6 text-secondary lh-global">
                        2019:2/3 ? <span class="text-primary"></span>
                    </p>
               </div>
               <div class="col-lg-6">
                   <div class="text-center  d-flex justify-content-center align-items-center">
                       <img src="static/img/about2.svg" alt="">
                   </div>
               </div>

           </div>

            <div class="row timeLine aboutContentItem flex-row-reverse preFadeinTop">
               
               <div class="col-lg-6">
                    <h3 class="h2 text-primary">
                        2019/04 - 
                    </h3>
                    <p class="h6 lh-global text-secondary">
                        <span class="text-primary"></span>
                    </p>
               </div>
               <div class="col-lg-6">
                   <div class="text-center d-flex justify-content-center align-items-center">
                       <img src="static/img/about3.svg" alt="">
                   </div>
               </div>

           </div>

           <div class="row timeLine aboutContentItem preFadeinTop">
               
               <div class="col-lg-6">
                    <h3 class="h2 text-primary text-right">
                        2020/10 - 
                    </h3>
                    <p class="h6 text-secondary lh-global">
                        
                    </p>
               </div>
               <div class="col-lg-6">
                   <div class="text-center d-flex justify-content-center align-items-center">
                       <img src="static/img/about4.svg" alt="">
                   </div>
               </div>

           </div>

           </div>


       </section>

        <section>
            <div class="container-fluid mb-5 px-5 preFadein">
                <div class="d-flex justify-content-center mb-5">
                    <h2 class="h2 text-primary">   </h2>
                </div> 
                <div class="row">

                    <div class="col md-6 my-5">
                        <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3614.148564952403!2d121.53281808512104!3d25.062953260335743!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3442a959a9ce781b%3A0xb0c2ef0be716c094!2z6KGM5aSp5a6u!5e0!3m2!1szh-TW!2stw!4v1609195474781!5m2!1szh-TW!2stw" width="100%" height="450" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
                    </div>

                    <div class="col-md-6 bg-light p-3">

                        <validation-observer v-slot="{ invalid }">
                            <form @submit.prevent="sendMessage" :class="{'pageDisabled' : messageAreaDisabled}">

                            <validation-provider rules="required" v-slot="{ errors , classes }">
                            <div class="form-group">
                            <label class="mb-2 text-secondary" for="name"></label>
                            <input id="name" type="text" name="" v-model="user.name"
                                class="form-control" :class="classes">
                            <span class="invalid-feedback"> {{ errors[0] }} </span>
                            </div>
                            </validation-provider>

                            <validation-provider rules="required|email" v-slot="{ errors , classes }">
                            <div class="form-group mt-3">
                            <label class="mb-2 text-secondary" for="email"></label>
                            <input id="email" type="email" name="" v-model="user.mail"
                                class="form-control" :class="classes">
                            <span class="invalid-feedback"> {{ errors[0] }} </span>
                            </div>
                            </validation-provider>

                            <validation-provider rules="required|min:8" v-slot="{ errors , classes }">
                            <div class="form-group mt-3">
                            <label class="mb-2 text-secondary" for="tel"></label>
                            <input id="tel" maxlength="10" type="tel" name="" v-model="user.tel"
                                class="form-control" :class="classes">
                            <span class="invalid-feedback"> {{ errors[0] }} </span>
                            </div>
                            </validation-provider>

                            <validation-provider rules="required" v-slot="{ errors , classes }">
                            <div class="form-group">
                            <label class="mb-2 text-secondary" for="textArea"></label>
                            <br>
                            <div>
                                <textarea name="" id="textArea" cols="30" rows="10"  style="width : 100%" v-model="user.message" :class="classes"></textarea>
                                <span class="invalid-feedback"> {{ errors[0] }} </span>
                            </div>
                            </div>
                            </validation-provider>

                            <div class="d-flex justify-content-end">
                            <button type="submit" class="btn btn-primary btn-lg text-white" :disabled="invalid"></button>
                            </div>
                            
                            </form>

                        </validation-observer>

                    </div>

                </div>

            </div>
        </section>


    </div>
</template>

<script>

import $ from 'jquery';

export default {
    data(){
        return{
            user : {
                name : '',
                mail : '',
                tel : '',
                message : ''
            },
            messageAreaDisabled : false
        }
    },
    methods : {
    openPage(){
        
        setTimeout(() => {
        $('.aboutTitle').addClass('fadeIn');
        }, 500);

        setTimeout(() => {
            $('.curtain').addClass('curtainShow');
        }, 5);
        
    },
    addAnimate(){
        const scrollPos = $(window).scrollTop();
        const windowHeight = $(window).height();

        $('.preFadein').each(function(){
            const itemPos = $(this).offset().top;

            if((scrollPos + windowHeight) > (itemPos + 100)){
                $(this).addClass('fadeIn');
            }
        })

        $('.preFadeinTop').each(function(){
            const itemPos = $(this).offset().top;

            if((scrollPos + windowHeight) > (itemPos + 300)){
                $(this).addClass('fadeIn');
            }
        })

        $('.preShake').each(function(){
            const itemPos = $(this).offset().top;

            if((scrollPos + windowHeight) > (itemPos + 150)){
                $(this).addClass('shake');
            }
        })
    },
    sendMessage(){
        const self = this;

        self.messageAreaDisabled = true;

        alert('');
    }
    },
    created(){
        this.openPage();
        window.addEventListener('scroll' , this.addAnimate);
    }
}
</script>


